* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

li {
  list-style: none;
}

h3{
  font-size: 1rem;
}

input{
  color: grey;
  font-size: 1.1rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* height: 300px; */
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;  
  /* Para que los bordes de las imagenes no se salgan del contenedor */
  overflow: hidden;
  width: 240px;
  height: 230px;
}

.card p{
  font-family: Helvetica, Arial, sans-serif;
  font-size: .8rem;
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
  width: 100%;
  height: 100%;
}